/*----------------------------------------------------------------------------
 Customizer
----------------------------------------------------------------------------*/
/* Alpha color picker module
--------------------------------------*/
/**
 * Alpha Color Picker CSS
 */
.customize-control-alpha-color .wp-picker-container .iris-picker {
  border-bottom: none;
}

.customize-control-alpha-color .wp-picker-container {
  max-width: 257px;
}

.customize-control-alpha-color .wp-picker-open + .wp-picker-input-wrap {
  width: 100%;
}

.customize-control-alpha-color .wp-picker-input-wrap input[type="text"].wp-color-picker.alpha-color-control {
  float: left;
  width: 195px;
}

.customize-control-alpha-color .wp-picker-input-wrap .button {
  margin-left: 0;
  float: right;
}

.wp-picker-container .wp-picker-open ~ .wp-picker-holder .alpha-color-picker-container {
  display: block;
}

.alpha-color-picker-container {
  border: 1px solid #dfdfdf;
  border-top: none;
  display: none;
  background: #FFF;
  padding: 0 11px 10px;
  position: relative;
}

.alpha-color-picker-container .ui-widget-content,
.alpha-color-picker-container .ui-widget-header,
.alpha-color-picker-wrap .ui-state-focus {
  background: transparent;
  border: none;
}

.alpha-color-picker-wrap a.iris-square-value:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.alpha-color-picker-container .ui-slider {
  position: relative;
  z-index: 1;
  height: 24px;
  text-align: center;
  margin: 0 auto;
  width: 88%;
  width: calc( 100% - 28px);
}

.alpha-color-picker-container .ui-slider-handle,
.alpha-color-picker-container .ui-widget-content .ui-state-default {
  color: #777;
  background-color: #FFF;
  text-shadow: 0 1px 0 #FFF;
  text-decoration: none;
  position: absolute;
  z-index: 2;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border: 1px solid #aaa;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  margin-top: -2px;
  top: 0;
  height: 26px;
  width: 26px;
  cursor: ew-resize;
  font-size: 0;
  padding: 0;
  line-height: 27px;
  margin-left: -14px;
}

.alpha-color-picker-container .ui-slider-handle.show-opacity {
  font-size: 12px;
}

.alpha-color-picker-container .click-zone {
  width: 14px;
  height: 24px;
  display: block;
  position: absolute;
  left: 10px;
}

.alpha-color-picker-container .max-click-zone {
  right: 10px;
  left: auto;
}

.alpha-color-picker-container .transparency {
  height: 24px;
  width: 100%;
  background-color: #FFF;
  background-image: url(../img/images/admin/transparency-grid.png);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4) inset;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  padding: 0;
  margin-top: -24px;
}

@media only screen and (max-width: 782px) {
  .customize-control-alpha-color .wp-picker-input-wrap input[type="text"].wp-color-picker.alpha-color-control {
    width: 184px;
  }
}

@media only screen and (max-width: 640px) {
  .customize-control-alpha-color .wp-picker-input-wrap input[type="text"].wp-color-picker.alpha-color-control {
    width: 172px;
    height: 33px;
  }
}

.bdbg-menu {
  display: flex;
  align-items: stretch;
  height: 100%;
  margin: 0;
}

.bdbg-menu,
.bdbg-menu ul {
  list-style: none;
  padding: 0;
}

.bdbg-menu a {
  text-decoration: none;
  color: #fff;
  justify-content: space-between;
}

.bdbg-menu__item--back {
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}

.bdbg-menu__item--back > a {
  justify-content: flex-start !important;
}

.bdbg-menu__item--back > a::before {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 1.5em;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
  content: "arrow_drop_down";
  transform: translate(-50%, 0%) rotate(90deg);
}

.bdbg-menu__item a {
  display: flex;
  padding: 0.5em 1em;
}

.bdbg-menu__item--level-0.bdbg-menu__item--has-children.is-submenu-visible a:after {
  transform: rotate(180deg);
}

.bdbg-menu__item--level-0.bdbg-menu__item--has-children > a::after {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 1.5em;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
  content: "arrow_drop_down";
  transition: all 0.2s;
}

.bdbg-menu__item--level-0 .bdbg-menu__item--has-children > a::after {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 1.5em;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  -webkit-font-feature-settings: "liga";
  -webkit-font-smoothing: antialiased;
  content: "arrow_drop_down";
  transform: translate(0%, 0%) rotate(-90deg) !important;
}

.bdbg-menu__item--level-0 > .bdbg-menu__submenu.is-visible {
  display: block;
}

.bdbg-menu__submenu {
  display: none;
  min-width: 10em;
  z-index: 99;
  background-color: #fff;
}

.bdbg-menu__submenu .bdbg-menu__item {
  padding: 0;
}

.bdbg-menu__submenu .bdbg-menu__item a {
  color: #333;
}

.bdbg-menu__submenu .bdbg-menu__item a:hover {
  background-color: #eee;
}

.bdbg-menu__submenu.is-open > li {
  display: none;
}

.bdbg-menu__submenu.is-open > .is-open {
  display: block;
}

.bdbg-menu__submenu.is-open > .is-open > .bdbg-menu__submenu {
  display: block;
}

.bdbg-menu__submenu.is-open > .is-open > a {
  display: none;
}

.bdbg-menu--main {
  flex-direction: row;
  justify-content: flex-end;
}

.bdbg-menu--main > .bdbg-menu__item {
  display: flex;
  align-items: stretch;
}

.bdbg-menu--main > .bdbg-menu__item > a {
  display: flex;
  align-items: center;
}

.bdbg-menu--main .bdbg-menu__item--level-0.bdbg-menu__item--has-children {
  position: relative;
}

.bdbg-menu--main .bdbg-menu__item--level-0 > .bdbg-menu__submenu {
  position: absolute;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  top: 110%;
  left: 0;
  padding: 0.4em 0;
  border-radius: 3px;
}

.bdbg-menu--side {
  flex-direction: column;
}

.bdbg-menu--side a {
  padding: 1em 2em;
  color: #333;
}

.bdbg-menu--side a:hover {
  background-color: #eee;
}

.bdbg-menu--side .bdbg-menu__item--level-0 .bdbg-menu__submenu > li > a {
  padding-left: 3em;
}

.bdbg-menu--side .bdbg-menu__submenu {
  background-color: #e9e9e9;
}

.bdbg-menu--footer {
  flex-direction: column;
}

.mdl-layout__drawer {
  width: auto;
}
